.App {
  text-align: center;
}
.container {
  padding: 1.5em;
  background: #fff;
  border-radius: 0.5em;
  max-width: 900px;
  margin: 2em auto;
}
.math-question-container {
  padding: 1.5em;
  font-size: 1.8em;
  font-weight: bold;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  border-radius: 0.5em;
  text-align: center;
}
.header-font {
  color: #b0b0b0;
  font-size: 1.5em;
}
.chatData {
  padding: 0.5em;
  border: 1px solid #c0c0c0;
  height: 220px;
  overflow: auto;
  text-align: left;
}
.chatBox {
  position:absolute;
  bottom:0;
  background: #FFF;
  right:0;
  height: 350px;
  padding: 0.5em;
  border: 0.5em solid #c0c0c0;
  width: 500px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



